import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../../../../../helpers';
import { ScriptLoaderService } from '../../../../../../../_services/script-loader.service';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../../../animations/round-anticlock.animation';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../../../animations/fly-in-out.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {ListComponent, SafeHtmlPipe} from '../../../../../list/list.component'
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import { FormControl, Validators, FormGroup, ValidationErrors } from '@angular/forms';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge'; 
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/filter' ;
import { ToastComponent } from '../../../../../../layouts/toast-notification/toast.component';

const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
     templateUrl: "./crmdays-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class CrmDaysAddComponent    {
    @HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;
	
	 
	 crmdaysId = 0;   
	 status;
	 crmdays = new CrmDaysData();  
	 crmdaysdocForm: FormGroup; 
	 bootstrapFormValues = [];
	 msg = [];
	 
	 public types = [];
	 public executives = [];
	
 


 constructor(private http: HttpClient, private router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.crmdaysId).subscribe(params=>{
			if(params.crmdaysId != undefined){
				this.crmdaysId=params.crmdaysId; 
				}
		}); 
	
	if(this.crmdaysId == 0){
		this.status="Add";
		this.populateDropDownData();
	}else{
		this.status="Update";
		this.populateData();
	}
	}
	
     ngOnInit() {
		this.crmdaysdocForm = new FormGroup({			
			'crmdaysId' : new FormControl(''),
			'crmdaysCrmtypeId': new FormControl(''),
			'crmtypeName' : new FormControl('', [ Validators.required]),
			'crmdaysExeType' : new FormControl('', [ Validators.required]),
			'crmdaysDaycount' : new FormControl('', [Validators.pattern(NUM_REGAX), Validators.required]),
			'crmdaysDesc' : new FormControl('', [ Validators.required]),
			'crmdaysScript' : new FormControl('', [ Validators.required]),
			'crmdaysActive':new FormControl(''),
			'crmdaysSoInactive':new FormControl(''),
			'crmdaysLostfollowup':new FormControl(''),
			'crmdaysEntryId' : new FormControl(''),
			'crmdaysEntryDate' : new FormControl(''),
			'crmdaysModifiedId' : new FormControl(''),
			'crmdaysModifiedDate' : new FormControl(''),
		});
  }
	  
	    get crmdaysCrmtypeId(){ return this.crmdaysdocForm.get('crmdaysCrmtypeId')};
	    get crmtypeName(){ return this.crmdaysdocForm.get('crmtypeName')};
	    get crmdaysExeType(){ return this.crmdaysdocForm.get('crmdaysExeType')};
	    get crmdaysDaycount(){ return this.crmdaysdocForm.get('crmdaysDaycount')};
	    get crmdaysDesc(){ return this.crmdaysdocForm.get('crmdaysDesc')};
	    get crmdaysScript(){ return this.crmdaysdocForm.get('crmdaysScript')};
	    get crmdaysActive(){ return this.crmdaysdocForm.get('crmdaysActive')};
	    get crmdaysSoInactive(){ return this.crmdaysdocForm.get('crmdaysSoInactive')};
	    get crmdaysLostfollowup(){ return this.crmdaysdocForm.get('crmdaysLostfollowup')};
	    get crmdaysEntryId(){ return this.crmdaysdocForm.get('crmdaysEntryId')};
	    get crmdaysEntryDate(){ return this.crmdaysdocForm.get('crmdaysEntryDate')};
	    get crmdaysModifiedId(){ return this.crmdaysdocForm.get('crmdaysModifiedId')};
	    get crmdaysModifiedDate(){ return this.crmdaysdocForm.get('crmdaysModifiedDate')};
	  
	  populateData(){
		const headers = new HttpHeaders({'Content-Type':'application/json'});
		
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/activitystatus-data',
									JSON.stringify({ crmdaysId :this.crmdaysId,
												   	requestType : "update" }),
						{ headers, withCredentials: true }).subscribe(
			 										data => {
			 													this.crmdays = data['populateData'][0];
															},
													(err) => {
																console.log("ERROR: "+err);
															 });			 
		}
	 
	 populateDropDownData(){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
			  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/contact',
						 			JSON.stringify({ emp_id :'0'}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.types = data['populateContactTitle']
			  															});
		     this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/executive',
						 			JSON.stringify({ emp_id :'0'}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.executives = data['populateExecutive']
			  															});
			   
			  
	 }
	   
	 
	  
	  ngAfterViewInit(){
		    $.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){
		   });
	  }
 
 	 toggleAnimation(){
			this.router.navigate(['portal/crmdays-list']);
		 }
		   

	onSubmit() {
			this.bootstrapFormValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFormValues.length;i++){
				this.crmdays[this.bootstrapFormValues[i].name]=this.bootstrapFormValues[i].value;
			}
		
		 if(!this.crmdaysdocForm.invalid){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
		if(this.crmdaysId == 0){ 
			this.crmdays['requestType'] = 'add';
		}else{
			this.crmdays['requestType'] = 'update';
			this.crmdays['crmdaysId'] = this.crmdaysId+'';
		}
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/activitystatus',
									JSON.stringify(this.crmdays),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'] == undefined){
														this.msg[0]='Error';
			 											this.msg[1]=data['errormsg'];
														}else{
														if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}
														}
			  											
			  											this.toast.showtoast(this.msg);
			  											if(this.msg[0]!='Error'){
			 											this.crmdaysdocForm.reset();
														this.toggleAnimation();
														}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
			
			}
	  }


  onDelete(){
		 this.crmdays['requestType'] = 'delete';
		 this.crmdays['crmdaysId'] = this.crmdaysId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/activitystatus',
									JSON.stringify(this.crmdays),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}else{
														this.msg[0]='Error';
			 											this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.crmdaysdocForm.reset();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
														});
		 
	 }
}

export class CrmDaysData {
	
	crmdaysId: string ;
    crmdaysCrmtypeId: string ;
	crmtypeName: string;
	crmdaysExeType: string;
	crmdaysDaycount: string;
	crmdaysDesc: string;
	crmdaysScript: string;
	crmdaysActive: string="true";
	crmdaysSoInactive: string;
	crmdaysLostfollowup: string;
	crmdaysEntryId:string;
	crmdaysEntryDate:string;
    crmdaysModifiedId:string;
    crmdaysModifiedDate:string;
	
}


